import React from 'react'
import {Link} from 'react-router-dom'
import { Transition } from '@windmill/react-ui'
import { MenuItems } from "./MenuItems";



const Dropdown = ({isOpen, toggle}) => {
  return (
    <div className={isOpen ? '' : 'hidden'} onClick={toggle}>
      <Transition
        show={isOpen}
        enter="transition ease-out transform"
        enterFrom="opacity-0 scale-95"
        enterTo="opacity-100 scale-100"
        leave="transition ease-out transform"
        leaveFrom="opacity-100 scale-100"
        leaveTo="opacity-0 scale-95"
      >

        <ul className="fixed mt-0 w-screen grid grid-rows-4 text-center items-center bg-brand text-black z-50 left-0 transform -translate-x-full transition duration-200 ease-in-out">
          {MenuItems.map((item, index) => {
                  return (
                      <Link
                        className={item.cName}
                        to={item.path}
                      >
                    <li
                      key={index}
                      className="p-4 focus:bg-black focus:text-brand hover:bg-black hover:text-brand"
                    >
                        {item.title}
                    </li>
                    </Link>
                  );
                })}

        </ul>

      </Transition>
    </div>
  )
}

export default Dropdown
