import React, { useState } from "react";
import emailjs from "emailjs-com";

const Result = () => {
  return <p>Messaggio inviato con successo</p>;
};

const ContactForm = () => {
  const [result, showResult] = useState(false);
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_yufj2uk",
        "template_tr6lzjo",
        e.target,
        "user_xvBrcs0OlwIfc94ZVM9I1"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
    showResult(true);
  };

  return (
    <div className="px-8 md:px-16 py-16 mx-auto max-w-3xl lg:py-20 mb-12 text-left">
      <form
        id="create-course-form"
        className="flex flex-col text-sm text-gray-300"
        onSubmit={sendEmail}
      >
        <label className="text-white mb-1">Nome</label>
        <input
          className="mb-3 bg-black text-white border-gray-300 border-2 p-1"
          type="text"
          name="user_name"
          required
        />
        <label className="text-white mb-1">Email</label>
        <input
          className="mb-3 bg-black text-white border-gray-300 border-2 p-1"
          type="email"
          name="user_email"
          required
        />
        <label className="text-white mb-1">Telefono</label>
        <input
          className="mb-3 bg-black text-white border-gray-300 border-2 p-1"
          type="tel"
          name="contact_number"
          required
        />
        <label className="text-white mb-1">Messaggio</label>
        <textarea
          className="mb-3 bg-black text-white border-gray-300 border-2 p-1"
          name="message"
          rows="4"
          required
        />
        <div className="mb-4">
          <span>
            <input
              className="mb-3 bg-gray-300 text-black p-1"
              name="presente"
              type="checkbox"
              required
            />
          </span>
          <span>
            <label className="text-white mb-1">
              {" "}
              Acconsento al trattamento dei miei dati per la finalità di
              Marketing.
            </label>
          </span>
        </div>
        <input
          className="transition duration-500 ease-in-out mb-3 p-2 bg-brand text-gray-300 hover:bg-brand-light hover:text-gray-700 cursor-pointer"
          type="submit"
          value="Invia"
        />
        <div className="text-center">{result ? <Result /> : null}</div>
      </form>
    </div>
  );
};

export default ContactForm;
