import React, { useState, useEffect } from "react";
import Navbar from "./components/Navbar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Dropdown from "./components/Dropdown";
import Footer from "./components/Footer";
import Home from "./pages";
import Contacts from "./pages/contacts";
import { Helmet } from "react-helmet";
import Serramenti from "./pages/serramenti";
import Pavimenti from "./pages/pavimenti";
import Creazioni from "./pages/creazioni";
import { Fragment } from "react";
import ScrollToTop from "./components/ScrollToTop";

const TITLE = "2MWOOD – Riqualificazione e Creazione";

export default function App() {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  useEffect(() => {
    const hideMenu = () => {
      if (window.innerWidth > 768 && isOpen) {
        setIsOpen(false);
      }
    };

    window.addEventListener("resize", hideMenu);

    return () => {
      window.removeEventListener("resize", hideMenu);
    };
  });

  document.body.style = "background: black;";

  return (
    <Router>
      <Helmet>
        <title>{TITLE}</title>
      </Helmet>
      <div className="App bg-black">
        <Navbar isOpen={isOpen} toggle={toggle} />
        <Dropdown isOpen={isOpen} toggle={toggle} />
        <Fragment>
          <ScrollToTop />
          <Switch>
            <Route exact path="/">
              {" "}
              <Home />{" "}
            </Route>
            <Route path="/serramenti">
              {" "}
              <Serramenti />{" "}
            </Route>
            <Route path="/pavimenti">
              {" "}
              <Pavimenti />{" "}
            </Route>
            <Route path="/creazioni">
              {" "}
              <Creazioni />{" "}
            </Route>
            <Route path="/contatti">
              {" "}
              <Contacts />{" "}
            </Route>
          </Switch>
        </Fragment>
        <Footer />
      </div>
    </Router>
  );
}
