import React from "react";
import Logo from "../assets/logo.svg";
import { Link } from "react-router-dom";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";
import { MenuItems } from "./MenuItems"


const Navbar = ({isOpen, toggle}) => {
  return (
    <div className="sticky top-0 z-50 bg-black relative w-full">
      <nav
        className="flex justify-between m-auto items-center h-24 text-gray-300 relative px-8 md:px-16 mx-auto max-w-7xl font-light"
        role="navigation"
      >
        <Link to="/" className="flex items-center">
          <img src={Logo} alt="" className="h-12" />
        </Link>
        <div className="pr-0 cursor-pointer lg:hidden" onClick={toggle}>
          <button className="text-gray-300">
            {isOpen ? <CloseIcon /> : <MenuIcon />}
          </button>
        </div>
        <div className="lg:block hidden">
          <ul className="flex">
          {MenuItems.map((item, index) => {
                  return (
                    <li
                      key={index}
                      className="p-4 hover:text-brand transition-colors duration-200"
                    >
                      <Link
                        className={item.cName}
                        to={item.path}
                      >
                        {item.title}
                      </Link>
                    </li>
                  );
                })}
          </ul>       
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
